import React, { useRef, useEffect } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import LandingPage from './components/LandingPage/LandingPage';
import Home from './components/Home/Home';
import About from './components/About/About';
import Projects from './components/Projects/Projects';
import Blog from './components/Blog/Blog';

function App() {
  gsap.registerPlugin(ScrollTrigger);
  const ref = useRef(null);
  useEffect(() => {
    const element = ref.current;

    gsap.fromTo(
      element.querySelector('.content-home-upper-content'),
      {
        opacity: 0,
        y: -50,
      },
      {
        opacity: 1,
        y: 0,
        duration: 1.5,
        scrollTrigger: {
          trigger: element.querySelector('.content-home-upper-content'),
          start: 'bottom bottom',
        },
      }
    );

    gsap.fromTo(
      element.querySelector('.content-home-lower-content'),
      {
        opacity: 0,
        y: 50,
      },
      {
        opacity: 1,
        y: 0,
        duration: 1,
        scrollTrigger: {
          trigger: element.querySelector('.content-home-lower-content'),
          start: 'bottom bottom',
        },
      }
    );
  }, []);
  return (
    <div ref={ref} className="App">
      <div className="navbar-wrapper">
        <Navbar />
      </div>
      <LandingPage />
      <Home id="home" />
      <About id="about" />
      <Projects id="projects" />
      <Blog id="blog" />
    </div>
  );
}

export default App;
